import React from "react";
// import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import "./Movie.css";

function SearchMovie(props) {
  const { id, year, title, poster, rating, director, actor } = props;

  return (
    <div className="movie">
      <a href={id} target="_black">
        <img src={poster} alt={title} title={title}></img>
        <div className="movie__data">
          <h3 className="movie__title">
            {title.replace(/<b>/gi, "").replace(/<\/b>/gi, "")}
          </h3>
          <p className="movie__rating">
            <span>평점 : </span> {rating} / 10
          </p>
          <p className="movie__year">
            <span>개봉년도 : </span> {year}년
          </p>
          <p className="movie__director">
            <span>감독 : </span> {director}
          </p>
          <p className="movie__actor">
            <span>배우 :</span> {actor}
          </p>
        </div>
      </a>
    </div>
  );
}
SearchMovie.propTypes = {
  id: PropTypes.string.isRequired,
  year: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  poster: PropTypes.string.isRequired,
  rating: PropTypes.string.isRequired,
  director: PropTypes.string.isRequired,
  actor: PropTypes.string.isRequired,
};
export default SearchMovie;
